html, body {
	background-color: black;
}

h1 {
  font-family: 'Lato', sans-serif;
  position: absolute;
  font-size: 3.8em; 
  color: white;
  font-weight: lighter;
}

section {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: black;
	text-align: center;
}

section, #particles-js {
	-ms-flex-align: center;
	    align-items: center;
	display: -ms-flexbox;
	display: flex;  
	-ms-flex-pack: center;  
	    justify-content: center;
	height: 100%;
	width: 100%;
}



